<template>
  <header class="header">
    <nav class="header__nav" v-if="openNav">
      <button class="header__nav-lang" @click="switchLang">{{ $i18n.locale}}</button>
      <h1 class="header__nav-title">{{ $t('notes') }}</h1>
      <button class="header__nav-search" @click="openNav = false">
        <img src="@/assets/img/search.svg" alt="">
      </button>
    </nav>
    <nav class="header__search" v-else>
      <button class="header__search-back" @click="openNav = true">
        <img src="@/assets/img/back.svg" alt="">
      </button>
      <input 
      type="text" 
      :placeholder="$t('placeholder')" 
      class="header__search-inp"
      v-model="search">
      <button class="header__search-clear">
        <img src="@/assets/img/x.svg" alt="">
      </button>
    </nav>
  </header>
</template>

<script>
  export default {
    data(){
      return{
        openNav: true,
        search: ''
      }
    },
    methods:{
      switchLang(){
        this.$i18n.locale == 'ru' ? this.$i18n.locale = 'uz' : this.$i18n.locale = 'ru'
        localStorage.lang = this.$i18n.locale
      }
    },
    watch:{
      search(newVal){
        console.log(newVal);
        this.$emit('filterNotes', newVal)
      }
    }
  }
</script>